import { ChangeDetectorRef, Component } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';


@Component({
  selector: 'app-detail-group',
  templateUrl: './detail-group.component.html',
  styleUrls: ['./detail-group.component.scss']
})
/** component ny  chn khch hng vo hng */
export class DetailGroupComponent {

  list_customer: any = [];
  list_search_customer: any = [];
  isVisibleModalAdd = false;
  group: any;
  keySearch: any; //bin lu tm key search
  customerGroups: any[] = [];
  list_customer_valid = this.vhQuerySales.getlocalCustomers().filter(item => item.type != 3).filter(item => !item.id_customer_class || item._id != 'id_retail')
  list_search_customer_valid: any = [];
  constructor(
    private router: Router,
    private vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private lang: LanguageService,
    private cdRef: ChangeDetectorRef,
    private vhQuery: VhQuery,
    private vhComponent: VhComponent

  ) {
    this.group = this.router.getCurrentNavigation().extras.state.group
  }
  ngAfterViewInit() {
    this.doRefreshData()
    this.customerGroups = this.vhAlgorithm.sortStringbyASC(this.vhQuerySales.getlocalCustomerClasss(), 'name')
  }
  /**
   * get cc khch hng thuc hng nhn dc t state truyn qua
   * @example this.doRefreshData()
  */
  doRefreshData() {
    this.list_customer = this.vhQuerySales.getlocalCustomers().filter(item => item.type != 3).filter(item => item.id_customer_class == this.group._id )
    this.list_search_customer = this.list_customer
  }
  heightScroll: string;
  ngAfterViewChecked() {
    if (document.querySelector("#detail-group-customers") && document.querySelector(".ant-table-thead") && document.querySelector(".detail-group-customers-header")) {
      this.heightScroll = document.querySelector("#detail-group-customers").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".detail-group-customers-header").clientHeight - 110 + "px";
    }
    this.cdRef.detectChanges();
  }
  /**
   * quay v trang danh sch hng kh
   */
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/partner/customer-group']);
  }

  // gotoDetailCustomer(data){
  //   this.router.navigate(['/sales/dashboard/manage/partner/customer-group/edit'],{state:{data:data, group : this.group}});
  // }

  /*---tm kim khch hng theo tn v email---*/
  searchCustomer(event) {
    let value = this.vhAlgorithm.changeAlias(event.toLowerCase());
    this.keySearch = value;
    this.list_customer = this.vhAlgorithm.searchList(
      value,
      this.list_search_customer,
      ["name", "phone", "email"]
    );
  }
  /*---tm kim khch hng theo tn v email---*/
  searchCustomerModal(event) {
    let value = this.vhAlgorithm.changeAlias(event.toLowerCase());
    this.list_customer_valid = this.vhAlgorithm.searchList(
      value,
      this.list_search_customer_valid,
      ["name", "phone", "email"]
    );
  }

  /**
   * cp nht li hng ca khch hng c chn
   * @param ev id_customerclass c chn
   * @param data khch c chn
   * @example this.groupChange('DNWJHDBHUYWGDWGDUWY',customer_selected)
   */
  groupChange(ev, data) {
    this.vhQuerySales.updateCustomer(data._id, { id_customer_class: ev ? ev : null })
      .catch((err) => { console.error(err) })
  }

  /**
   * tt modal chn khch hng vo hng
   * @example this.handleCancel()
   */
  handleCancel() {
    this.isVisibleModalAdd = false;
  }

  /**
   * x l sk nhn nt ok modal chn khch hng vo hng
   * @example this.handleOk() 
   */
  handleOk() {
    this.vhComponent.showLoading('')
      .then(() => {
        let promise = new Array();
        let arr = this.list_customer_valid.filter(item => item.checked)
        for (let i = 0; i < arr.length; i++) {
          promise[i] = this.vhQuerySales.updateCustomer(arr[i]._id, { id_customer_class: this.group._id })
        }
        this.vhQuery.asyncPromiseAll(promise)
          .then((array) => {
            this.vhComponent.hideLoading(0)
            this.doRefreshData()
            this.isVisibleModalAdd = false;
          }, error => {  })
      })

  }

  /**
   * m modal chn khch hng vo hng
   * @example this.openModal()
   */
  openModal() {
    this.isVisibleModalAdd = true;
    this.list_customer_valid = this.vhQuerySales.getlocalCustomers().filter(item => item.type != 3).filter(item => !item.id_customer_class && item._id != 'id_retail').map(ele => { return { ...ele, checked: false } })
    this.list_search_customer_valid = this.list_customer_valid
  }


  /** hm sort cho cc ct */
  nameCol = false;
  phoneCol = false;
  emailCol = false;
  addressCol = false;
  /** Hm thc hin sp xp theo collection
   *
   * @param colName       // tn ct mun sp xp
   */
  sortTable(colName){
    switch (colName) {
      case 'name':
        if(this.nameCol){
          this.list_customer = this.vhAlgorithm.sortVietnamesebyASC([...this.list_customer], colName);
        }else{
          this.list_customer = this.vhAlgorithm.sortVietnamesebyDESC([...this.list_customer], colName);
        }
        break;
      case 'phone':
        if(this.phoneCol){
          this.list_customer = this.vhAlgorithm.sortNumberbyASC([...this.list_customer], colName);
        }else{
          this.list_customer = this.vhAlgorithm.sortNumberbyDESC([...this.list_customer], colName);
        }
        break;
      case 'email':
        if(this.emailCol){
          this.list_customer = this.vhAlgorithm.sortStringbyASC([...this.list_customer], colName);
        }else{
          this.list_customer = this.vhAlgorithm.sortStringbyDESC([...this.list_customer], colName);
        }
        break;
      case 'address':
        if(this.addressCol){
          this.list_customer = this.vhAlgorithm.sortVietnamesebyASC([...this.list_customer], colName);
        }else{
          this.list_customer = this.vhAlgorithm.sortVietnamesebyDESC([...this.list_customer], colName);
        }
        break;
    }
  }
}

