import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { IonicModule } from '@ionic/angular';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { CustomerGroupComponent } from './customer-group.component';
import { DetailGroupComponent } from './detail-group/detail-group.component';
import { EditGroupComponent } from './edit-group/edit-group.component';

const routes: Routes = [
  {
    path: '',
    component: CustomerGroupComponent
  },
  {
    path: 'detail',
    component: DetailGroupComponent
  },
  {
    path: 'edit',
    component: EditGroupComponent
  }
]

@NgModule({
  declarations: [
    CustomerGroupComponent,
    DetailGroupComponent,
    EditGroupComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    ScrollingModule,
    NgZorroAntModule,
    FormsModule,
    ReactiveFormsModule,
    TranslateModule,
    IonicModule
  ]
})
export class CustomerGroupModule { }
